$purpleColor: #802C6E;
$orangeColor: #F7AA35;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 18px;
  color: black;
}

a, input, button {
  font-family: inherit;
  color: inherit;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
}

.navbar {
  width: 100%;
  height: 80px;
  padding: 20px 100px;
  background: $purpleColor;
  display: flex;
  align-items: center;
  color: white;

  h2 {
  margin-right: 200px;
  }

  .navbar__list {
    display: flex;
    list-style: none;
    font-size: 20px;
    font-weight: bold;

    .navbar__item:hover {
      color: $orangeColor;
      cursor: pointer;
    }
    
    .navbar__item:not(:last-child) {
      margin-right: 50px;
    }
  }
}

.dropdown__trigger {
  position: relative;
}

.dropdown__menu {
  position: absolute;
  left: -10%;
  display: flex;
  flex-direction: column;
  list-style-type: none;
  padding: 5px 10px;
  border-radius: 10px;
  background: #ccc;

  .dropdown__item {
    text-align: center;
    color: white;
    border-bottom: 1px solid $orangeColor;
  }

  .dropdown__item:hover {
    color: $orangeColor;
  }
}

.wrapper {
  padding: 30px 300px;
}

.page-title {
  margin-bottom: 30px;
}

.view-table__list {
  display: flex;
  flex-wrap: wrap;
}

.view-table__item {
  width: 200px;
  height: fit-content;
  padding: 15px;
  margin-right: 60px;
  margin-bottom: 30px;
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
}

.view-table__img, .view-table__title {
  cursor: pointer;
}

.view-table__img {
  width: 100%;
  height: 200px;
}

.view-card__img {
  width: 400px;
  height: 500px;
}

.view-table__title {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.view-table__price {
  margin-top: 10px;
  display: block;
  text-align: right;
  font-weight: bold;
}

.view-card__block {
  display: flex;
  justify-content: space-between;
}

.view-card__content {
  width: 800px;

  h3 {
    margin-bottom: 30px;
  }
}